export const handleAddBook = (event) => {
  const user = localStorage.getItem('user') || '';
  const button = document.querySelector('.add-book');
  button.addEventListener('click', (event)  => {
    event.preventDefault();
    const bookElement = document.querySelector('.book-text')
    const authorElement = document.querySelector('.author-text')
    const book = bookElement.value;
    const author  = authorElement.value;
    

    let dataArray = localStorage.getItem('data') ? JSON.parse(localStorage.getItem('data')) : [];
    let formattedData = {};

    if(!dataArray.length) {
      formattedData = {
        id: 1,
        book,
        author,
        lender: user,
        borrower: '',
        actions: [],
        isBorrowed: false
      }
    } else {
      formattedData = {
        id: dataArray.length+1,
        book,
        author,
        lender: user,
        borrower: '',
        actions: [],
        isBorrowed: false
      }
    }
    dataArray.push(formattedData)


    

    localStorage.setItem("data", JSON.stringify(dataArray))
    window.location.reload();
  })
}