const id = data.length ? data.length + 1 : 1;


export const TableHeader = () => {
  const headerValues = ['Id', ' Book', 'Author', 'Lender', 'Borrower', 'Action'];
  const headerDetails = headerValues.map(item => `<div class="bookTable-header-${item.toLowerCase()}">${item}</div>`);
  return headerDetails.join('');
}

export const getData = () => {
  if(!data.length) {
    return `<div></div>`;
  }
  let tableValue = data.map(item => `<div class="tableDetails-wrapper"><div>${item.id} </div> <div>${item.book} </div> <div>${item.author}</div> <div>${item.lender}</div> <div>${item.borrower}</div></div>`);
  return tableValue.join('');
}

export const TableDetails = () => {
  return `<div class="bookTable-details-wrapper">
  ${getData() }
  ${enterData()}
  </div>`
}

export const enterData = () => {
  const user = localStorage.getItem('user') || '';
  return `<div class="tableDetails-wrapper">
        <div>${id}</div>
        <input type="text" name="book" value="" class="book-text"/>
        <input type="text" name="author" value="" class="author-text"/>
        <div>${user}</div>
        <div>
          <button class="add-book">Add Book</button>
        </div>
    </div>
  </div>`
}





export const bookTable = () => {
  return `<div class="bookTable-container">
   <div class="bookTable-wrapper"> 
     ${TableHeader()}
    </div>
    <div class="bookTable-table-details-wrapper">
    ${TableDetails()}
    </div>
  </div>`
}
